
























import HeadTtop from '../../components/head/head.vue'

export default {
	components: {
		'HeadTtop': HeadTtop
	},
	data() {
		return {
			Title: '登录',
			backshow: false,
			tel: '',
			pas: '',
		}
	},
	methods: {
		registerClick() {
			uni.navigateTo({
				url: '/pages/register/register'
			});
		},
		forget() {
			uni.navigateTo({
				url: '/pages/forgetpas/forgetpas'
			});
		},
		loginClick() {
			const that = this;
			const myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
			if (!myreg.test(that.tel)) {
				uni.showToast({
					title: '请输入正确手机号',
					duration: 1000
				});
				return false
			}
			if (that.tel === '') {
				uni.showToast({
					title: '请输入手机号',
					icon: 'success',
					mask: true,
					duration: 1000
				});
				return false
			}
			if (that.pas === '') {
				uni.showToast({
					title: '请输入密码',
					icon: 'success',
					mask: true,
					duration: 1000
				});
				return false
			}
			const params = {
				tel: that.tel,
				password: that.pas
			}
			this.$http.HttpRequst.requestLogin(true, 'user/login', params, 'POST', res => {
				//console.log(res)
				if (res.code === 200) {
					uni.setStorage({
						key: 'userinfo',
						data: res.data
					});
				}
				uni.switchTab({
					url: '/pages/index/home/home'
				});
			})
		}
	}
}
